<template lang='pug'>
v-container(fluid)
  v-layout(column wrap align-center)
    h1 You have logged in to “Where am I?” Please enter your details to continue. 
    v-flex()
      h2 What is your email address? 
      v-text-field(label='Email address' v-model='email.answer' :append-icon="email.success ? 'check_circle' : '' " :success='email.success' :error='!email.success')
    v-flex
      h2 What is the name of your school?
      v-text-field(label='School Name' v-model='school.answer' :append-icon="school.success ? 'check_circle' : '' " :success='school.success' :error='!school.success')
    v-flex
      h2 What is the name of your street? 
      v-text-field(label='Street Name' v-model='street.answer' :append-icon="street.success ? 'check_circle' : '' " :success='street.success' :error='!street.success')
    v-flex
      h2 When is your birthday? (in format DD/MM/YYYY)
      v-text-field(label='Birthday' v-model='birthday.answer' :append-icon="birthday.success ? 'check_circle' : '' " :success='birthday.success' :error='!birthday.success')
    v-btn(large id='submit' @click='onSubmit()') Submit
    v-flex
      h1(v-if='hint.visibility' id='hintText') {{ hint.text }}
      v-btn(large id='hint' @click='hint.visibility=true') Hint
    v-dialog(persistent v-model='success' width='80%' id='dialog')
      v-card
        v-card-title 
          h2 Success! I am here:
        v-card-text 
          img(src='../assets/map.jpg' width='100%')
</template>

<script>
export default {
  data() {
    return {
      email: { answer: "", success: false },
      school: { answer: "", success: false },
      street: { answer: "", success: false },
      birthday: { answer: "", success: false },
      success: false,
      hint: {
        text: "Take a look at my Friendbook Page to find the answers!",
        visibility: false
      }
    };
  },
  methods: {
    onSubmit() {
      if (this.email.answer == "ben@bucrew.com") {
        this.email.success = true;
      }
      if (
        this.school.answer == "Hill Primary School" ||
        this.school.answer == "hill primary school" ||
        this.school.answer == "hill primary" ||
        this.school.answer == "Hill Primary"
      ) {
        this.school.success = true;
      }
      if (
        this.street.answer == "Mill Lane" ||
        this.street.answer == "mill lane"
      ) {
        this.street.success = true;
      }
      if (this.birthday.answer == "02/09/2008") {
        this.birthday.success = true;
      }

      if (
        this.email.success === true &&
        this.school.success === true &&
        this.street.success === true &&
        this.birthday.success === true
      ) {
        this.success = true;
      }
    },
    hint() {
      this.hint.visibility = true;
    }
  }
};
</script>

<style scoped>
h1 {
  text-align: center;
  padding-bottom: 40px;
}
h2 {
  text-align: center;
}
#dialog {
  text-align: center;
}
#hint {
  position: absolute;
  bottom: 20px;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  width: 300px;
  background-color: #b71c1c;
  color: white;
}
#hintText {
  padding-top: 80px;
}
#submit {
  width: 300px;
  background-color: #42b983;
  color: white;
}
</style>



